import React from "react";
import { Link } from "react-router-dom";

import styles from "./Footer.module.scss";
import { ReactComponent as LogoIcon } from "../images/logo/vt-red.svg"; 
import { ReactComponent as DiscordIcon } from "../images/icons/discord.svg";
import { ReactComponent as TwitterIcon } from "../images/icons/twitter.svg";
import { ReactComponent as InstagramIcon } from "../images/icons/instagram.svg";
import { ReactComponent as SteamIcon } from "../images/icons/steam.svg";

function Footer() {
  return (
    <div className={styles.footer}> 
      <div className={styles.hr}></div>
      
      <div className={styles.footerBody}>
        
        <div className={styles.footerLogoWrapper}>
          <LogoIcon style={{ width: "150px", height: "50px"}} />
          <p>Copyright  2020 Virtrade,<br /> Inc. All rights reserved <br /><br /><span>info@virtrade.com</span></p>
        </div>

        <div className={styles.footerInfoSection} style={{marginLeft: "200px"}}>
          <h4>CUSTOMER INFORMATION</h4>
          <Link>Support</Link>
          <Link>About us</Link>
          <Link to="/terms">Terms and conditions</Link>
        </div>

        <div className={styles.footerInfoSection} style={{marginLeft: "100px"}}>
          <h4>GUIDELINES</h4>
          <Link>FAQ</Link>
          <Link>Trading rules</Link>
          <Link>Reputation rules</Link>
          <Link>Prevent scam</Link>
        </div>

        <div className={styles.footerSocials}>
          <h4>Follow us</h4>
          <div>
            <DiscordIcon style={{ height: "20px", width: "20px", marginRight: "15px" }} />
            <TwitterIcon style={{ height: "20px", width: "20px", marginRight: "15px" }} />
            <InstagramIcon style={{ height: "20px", width: "20px", marginRight: "15px" }} />
            <SteamIcon style={{ height: "20px", width: "20px", marginRight: "15px" }} />
          </div>
        </div>

      </div>
    </div>
  );
}

export default Footer;
